@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800;900&display=swap);
.navigation {
  display: grid;
  grid-template-columns: 15% 70% 15%;
  margin: 0 1.5rem;
  height: 5rem;
  border-bottom: 1px solid var(--gray-border);
}

.navigation img {
  height: 4rem;
  margin-left: 1rem;
}

.entertainment {
  position: relative;
}

ul {
  visibility: hidden;
  opacity: 0;
  position: absolute;
  top: 4.6em;
  left: 0;
  display: none;
  width: 80%;
  height: 7.5rem;
  border-right: 1px solid var(--gray-border);
  border-left: 1px solid var(--gray-border);
  border-bottom: 1px solid var(--gray-border);
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
  background-color: var(--bg-black);
}

.entertainment:hover > ul {
  -webkit-animation: fadeIn 0.7s;
          animation: fadeIn 0.7s;
}

.entertainment:hover > ul,
ul:hover {
  visibility: visible;
  opacity: 1;
  display: grid;
  align-items: center;
}

ul li {
  clear: both;
  width: 100%;
}

li {
  list-style: none;
}

li > a {
  color: var(--gray-text);
  text-decoration: none;
}

li:hover > a {
  color: var(--green-selector);
}

.brand {
  display: flex;
  align-items: center;
}

.navigation-options {
  display: flex;
  justify-content: center;
}

.navigation-options div {
  text-transform: uppercase;
  display: flex;
  align-items: center;
  height: 100%;
  margin: 0 1.7rem;
  font-size: 0.9rem;
  font-weight: 800;
  letter-spacing: 1px;
  cursor: pointer;
}

.navigation-options div a {
  text-decoration: none;
  color: var(--gray-text);
}

.navigation-options div a:hover {
  color: var(--green-selector);
}

.navigation-options div:hover {
  color: var(--green-selector);
}

.search {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.serachContainer {
  background-color: var(--gray-input);
  display: flex;
  margin-right: 1rem;
  width: 85%;
  height: 2.15rem;
  align-items: center;
  border-radius: 10px;
}

.textInput input {
  background-color: transparent;
  color: var(--gray-text);
  border: 0;
  height: 1.5rem;
  width: 90%;
  margin-left: 0.3rem;
  border-radius: 10px;
}

.textInput input:focus {
  outline: none;
}

.fa-search {
  margin-right: 0.5rem;
}

.monster-category {
  display: flex;
  height: 6rem;
  justify-content: center;
}

.category-option {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  margin: 0 4rem;
  width: 10rem;
}

.category-option:hover > img {
  transition: all 0.4s ease-out;
  height: 4.5rem;
}

.category-option img {
  cursor: pointer;
  height: 4rem;
  transition: all 0.4s ease-in;
}

.monster-ultra-active {
  border-bottom: 2px solid var(--gray-border);
}

.monster-original-active {
  border-bottom: 2px solid var(--green-selector);
}

.monster-juice-active {
  border-bottom: 2px solid #ffb102;
}

.monster-container {
  display: flex;
  height: calc(100vh - 11.3rem);
  width: 100%;
  justify-content: center;
  align-items: center;
}

.monster-presentation {
  display: grid;
  grid-template-columns: 50% 50%;
  height: 98%;
  width: 90%;
  align-content: center;
  justify-items: center;
}

.animation-container {
  -webkit-animation: fadeIn 2.5s;
          animation: fadeIn 2.5s;
}

.monster-description {
  text-align: justify;
  display: flex;
  flex-flow: column;
  height: 28rem;
  width: 25rem;
}

.brand-banner {
  display: flex;
  justify-content: center;
  margin: 1rem 0;
  height: 7rem;
}

.brand-banner img {
  height: 100%;
}

.description {
  font-size: 1rem;
}

.description > p:last-child {
  font-weight: 700;
  letter-spacing: 0.5px;
}

.monster-type {
  display: flex;
  width: 70%;
  align-items: center;
  justify-content: space-between;
}

.monster-type div {
  cursor: pointer;
}

.inactive > img {
  transition: all 0.4s ease-in;
}

.inactive:hover > img {
  transition: all 0.4s ease-out;
  height: 10em;
}

.monster-type img {
  height: 100px;
}

.active-drink {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 12rem;
}

.active-drink img {
  -webkit-animation: monster-expand 1s;
          animation: monster-expand 1s;
  height: 370px;
}

:root {
  --bg-black: #0e0e0e;
  --gray-text: #e9e9e9;
  --gray-border: #c6c6c6;
  --green-selector: #91c840;
  --gray-input: rgb(106, 106, 106, 0.5);
}

html,
body {
  margin: 0;
  padding: 0;
}

body {
  background-color: #0e0e0e;
  background-color: var(--bg-black);
  color: #e9e9e9;
  color: var(--gray-text);
  font-family: "Montserrat", sans-serif;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes monster-expand {
  from {
    height: 10em;
  }
  to {
    height: 370px;
  }
}

@keyframes monster-expand {
  from {
    height: 10em;
  }
  to {
    height: 370px;
  }
}

